import { Box } from "@chakra-ui/react";
import '../Inspirations.css';

const KitchenFlooring = () => {
    return (
        <Box maxWidth="8xl" margin="30px 60px" display="flex" flexDirection="column" gap="1.2rem">
            <h3 style={{ fontSize: "22px" }}><strong>3 Affordable options for kitchen flooring in Malaysia</strong></h3>
            <hr></hr>
            <p>Next to the bathroom, the kitchen is one area in the house that receives the most heavy-duty use.</p>
            <p>There are oil spills, water drips, and food crumbs. It is also a place where you cook together with your spouse, your toddlers waddle in taking a peek, and your pets run around in.</p>
            <p>As such, when choosing kitchen floor tiles, it is important to consider their characteristics as much as the looks.</p>
            <p>In this article, we will go through three types of high-quality kitchen tiles that are cheap and durable for your Malaysian homes.</p>
            <h2 className="has-text-color">1. Kitchen tiles with wooden effect</h2>
            <img style={{ maxWidth: "400px" }} src="https://lorenzaceramica.com/wp-content/uploads/2021/03/3d-rendering-loft-modern-black-kitchen-with-wood-d-EVXH3AN-1024x683.jpg" alt="post-banner"></img>
            <p>Due to its traditional look, hardwood flooring has been making a comeback in Malaysia, reminiscent of <em>kampung</em>-styled homes made of <em>jati</em> (teak) and <em>nyatuh</em> tree.</p>
            <p>However, where it shines in appearance, hardwood flooring comes with a steep price. Due to the intricate nature of the installation, you might also have to hire professionals rather than doing it by yourself.</p>
            <p></p>
            <p>So, if you like the look of hardwood flooring, but the cost makes you floored, tiles with wooden tones are always the perfect and affordable alternative.</p>
            <p>Plus, you don’t have to deal with water problems that can easily damage hardwood flooring. Not to mention, cleaning it up is much easier!</p>
            <p>The classic and elegant wooden design can fit nicely into any kind of kitchen style, such as modern, contemporary, or country. </p>
            <p>Tiles with wooden effect come in various designs to mimic the grain patterns of real wood. You can view some examples <a href="https://lorenzaceramica.com/products/tiles/wood"><span style={{ textDecoration: "underline" }}><span style={{ color: "#005399" }} className="has-inline-color">here</span></span></a>. Lay and arrange them out strategically to achieve your ideal Pinterest aesthetic.</p>
            <h2 className="has-text-color">2. Kitchen tiles with Moroccan design</h2>
            <img style={{ maxWidth: "400px" }} src="https://lorenzaceramica.com/wp-content/uploads/2021/03/03598567defca0c2fbdade8d7fa4c068-828x1024.jpg" alt="post-banner"></img>
            <p>Image: <a href="https://in.pinterest.com/pin/33847434687268523/"><span style={{ textDecoration: "underline" }}><span style={{ color: "#005399" }} className="has-inline-color">Pinterest</span></span></a></p>
            <p>If you want to go bold, you can go gold with Moroccan-style tiles. These Arabic designs typically feature bright colours and geometric designs, making your kitchen a brighter and more fun place to cook.</p>
            <p>You can also reinforce the floor design with your kitchen backsplash, adding a strong character to the space. The rest of the kitchen (walls, kitchen cabinet) can feature a soft, muted colour with a clean design to complement the dramatic look of your Moroccan tiles. </p>
            <p>Like most tiles, Moroccan tiles are relatively easy to maintain. Most stains and spills can be cleaned with water and regular clothes, while the tougher stains can be removed with bicarbonate soda.</p>
            <h2 className="has-text-color">3. Kitchen tiles with matt finishing</h2>
            <p>Tiles come in different categories, mainly their style, design, and colour. However, there is another grouping of tiles that you should consider – especially when renovating your kitchen – which is gloss vs matt finishing.</p>
            <p>Gloss tiles are reflective which allow lights to bounce, making the space lighter and appear bigger.</p>
            <p>However, this type of finishing can be slippery. Since we Malaysians love our deep-fried nuggets and <em>keropok lekor</em>, oil splotches and spills can cause the floor to be sticky.</p>
            <img style={{ maxWidth: "400px" }} src="https://lorenzaceramica.com/wp-content/uploads/2021/03/kitchen-with-tile-floor-1024x720.jpg" alt="post-banner"></img>
            <p>Image: <a href="https://www.pinterest.com/pin/191262315400136745/"><span style={{ textDecoration: "underline" }}><span style={{ color: "#005399" }} className="has-inline-color">Pinterest</span></span></a></p>
            <p>Tiles with matt finishing, on the other hand, have more practical benefits for your kitchen.</p>
            <p>To start, the surface has a better grip, making it safer. Stains are not as noticeable on matt surfaces, but if there is, it can be harder to remove.</p>
            <p>As for the look, matt tiles have that industrial feel, akin to your typical artisan cafés in Klang Valley. Now, all you need to complete the rustic aesthetics are some low-fi Spotify list, and a bicycle hanging on a wall for some reason.</p>
            <h2 className="has-text-color">Which style of kitchen floor do you prefer?</h2>
            <p>If you’re planning to do a kitchen renovation, the flooring is certainly a big part of the look and feel. Other areas include the kitchen countertop and backsplash, which we will go into in the future.</p>
        </Box>
    )
}

export default KitchenFlooring;